.navbar {
  background-color: #0080a7 !important;
}

.Navbar__content{
  position: relative;
  width: 100%;
  margin-top: 17px;
}

.Navbar {
  text-align:center;
  height: 60px;
  width: 100%;
  position: absolute;
  z-index: 10; 
  display:-webkit-flex !important; 
  display:flex !important;
}
.Navbar__title{
  position: absolute;
  top: -10px;
  left: 30%;
}

.Navbar__img{
  margin-left: 3%;
  position: relative;
  float: left;
}

.Navbar__img img{
    margin: 12px 0px 0px 20px;
    height: 30px;
}

.Navbar__element a{
  text-decoration: none;
  color:rgba(245, 245, 245, 0.637);
  position: relative;
  overflow: hidden;
  display: inline-block;
  text-decoration: none;
}

  .Navbar__element a:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: white;
  transition: left .8s;
  }
  .Navbar__element a:hover {
    color: white;
    transition: 200ms ease-in;

  }

  .Navbar__element a:hover:after {
    left: 0;
  }

.Navbar__element p{
  margin-left: 30px;
  margin-right: 30px;
}

.Navbar__elements{
  text-align:justify;
}



