@media(max-width: 1000px){

    body{
        height: 100%;
    }

    .top img{
        width: 100%;
        /* height: 25em; */
        margin-bottom: 1em;
    }
    .image{
        
        display: none;
    }

    /* Security */
    .top h1{
        /* position: absolute; */
    
        font-style: normal;
        letter-spacing: 0.3em;
        text-transform: uppercase;
    
        display: flex;
        align-items: flex-end;
        justify-content: right;
    
        /*margin-left: -10%;*/
        /* margin-right: 10%; */
        margin-top: 20%;
    
        /* color: white; */
        font-size: 48px;
        color: var(--blue);
    }
    
    .text-gp{
        /* position: absolute; */
        /* margin: 30px; */
        margin-top: 0;
        /* display: flex; */
    }
    
    .top{
        margin: 0;
        padding: 0;
    
        width: 100%;
        
        padding-bottom: 30px;
    }
    
    .quote{
        margin-top: 5%;
        display: block;
        justify-content: center;
        padding-bottom: 100px;
    }
}

@media(min-width: 999px){
    body{
        height: 100%;
        /* padding: 1%; */
    }
    
    .top img{
        width: 100%;
        margin-bottom: 1em;
        /* margin-top: -11em; */
    }

    .image{
        position: relative;
        z-index: 1 !important; 
    }

    .top{
        width: 100%;
        height: 100%;
    }

    .title_security{
        position: absolute;

        z-index: 2 !important;

        font-style: normal;
        font-weight: 800;
        letter-spacing: 0.9em;
        text-transform: uppercase;
            
        font-size: 48px;
        color: white;
        
        margin-top: 6em;

        
    }

    .top_h1{
        display: flex;
        justify-content: left;
    }
    
    .text-gp{
        display: flex;
    }
    
    .quote{
        width: 100%;
        display: flex;
        justify-content: center;
        position: relative;
        line-height: 54px;
        text-align: center;
        letter-spacing: 0.04em;
        margin-bottom: 184px;
        
    }

    .quote h2{
        margin-top: 1%;

    }

}